'use strict';
export let plugins = {
  filterArrByArr: function filterArrByArr(
    arrayWithElements = [],
    arrayForFiltering = [],
    arrayForPushing = [],
    param,
  ) {
    // *    
    let foundedEl = {};
    for (let i = 0; i < arrayWithElements.length; i += 1) {
      foundedEl = arrayForFiltering.find(
        el => el[param] === arrayWithElements[i]
      );
      // * 
      if (foundedEl !== undefined) {
        arrayForPushing.push(foundedEl);
      }
    }
  },
};
